<template>
  <div class="container" @click="gallaryClick">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(item, index) in imgs" :key="index">
          <img class="swiper-img" :src="item">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: "Gallary",
  props: {
    imgs: Array
  },
  data() {
    return {
      swiperOption: {
        pagination: ".swiper-pagination",
        paginationType: "fraction",
        observer: true,
        observeParents: true
      }
    };
  },
  methods: {
    gallaryClick() {
      this.$emit("close");
    }
  }
};
</script>

<style scoped>
.wrapper >>> .swiper-container {
  overflow: inherit;
}
.container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 99;
}
.wrapper {
  height: 0;
  width: 100%;
  padding-bottom: 75%;
}
.swiper-img {
  width: 100%;
}
.swiper-pagination {
  color: #fff;
  bottom: -3.5rem;
}
</style>
