<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button" @click="ChangeCity(cur_city)">{{cur_city}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper" v-for="item in hotcities" :key="item.id">
            <div class="button" @click="ChangeCity(item.name)">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(item, key) in cities" :key="key" :ref="key">
        <div class="title border-topbottom">{{key}}</div>
        <ul class="item-list">
          <li
            class="item border-bottom"
            v-for="inner in item"
            :key="inner.id"
            @click="ChangeCity(inner.name)"
          >{{inner.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
import { mapState, mapMutations } from "vuex";
export default {
  name: "CityList",
  props: {
    cities: Object,
    hotcities: Array,
    targetArea: String
  },
  methods: {
    ...mapMutations({
      changeCity: "changeCity"
    }),
    ChangeCity(city) {
      // 相当于 this.$store.commit('changeCity',city)
      this.changeCity(city);
      this.$router.push("/");
    }
  },
  mounted() {
    this.scroll = new Bscroll(this.$refs.wrapper);
  },
  watch: {
    targetArea() {
      if (this.targetArea) {
        const targetCity = this.$refs[this.targetArea][0];
        this.scroll.scrollToElement(targetCity);
      }
    }
  },
  computed: {
    ...mapState({
      cur_city: "city"
    })
  }
};
</script>

<style scoped>
.border-bottom::before {
  border-color: #ccc;
}
.list {
  position: absolute;
  top: 1.52rem;
  right: 0;
  left: 0;
  bottom: 0;
  overflow: hidden;
}
.title {
  background-color: #eee;
  line-height: 0.6rem;
  padding-left: 0.2rem;
  color: #666;
  font-size: 0.26rem;
}
.button-list {
  overflow: hidden;
  padding: 0.1rem 0.6rem 0.1rem 0.1rem;
}
.button-wrapper {
  float: left;
  width: 33.33%;
}
.button {
  margin: 0.1rem;
  padding: 0.1rem 0;
  text-align: center;
  border: 0.02rem solid #ccc;
  border-radius: 0.06rem;
}
.item {
  line-height: 0.76rem;
  padding-left: 0.2rem;
}
</style>
