<template>
  <div class="banner" @click="checkGallary">
    <img
      class="banner-img"
      :src="this.bannerImgData"
    >
    <div class="banner-info">
      <div class="banner-title">{{this.sightNameData}}</div>
      <div class="img-info">
        <span class="iconfont">&#xe64a;</span>
        {{imgsDataNum}}
      </div>
    </div>
    <gallary :imgs='imgsData' v-show="showGallary" @close='closeGallary'></gallary>
  </div>
</template>

<script>
import Gallary from "../common/gallary/Gallary";
export default {
  name: "Banner",
  components: {
    Gallary
  },
  props:{
    sightNameData:String,
    bannerImgData:String,
    imgsData:Array
  },
  data() {
    return {
      showGallary:false
    };
  },
  methods: {
    checkGallary(){
      this.showGallary=true
    },
    closeGallary(){
      this.$nextTick(() => {
        this.showGallary=false
      })
    }
  },
  computed: {
    imgsDataNum(){
      return this.imgsData.length
    }
  }
};
</script>

<style scoped>
.banner {
  height: 0;
  overflow: hidden;
  padding-bottom: 55%;
  position: relative;
}
.banner-img {
  width: 100%;
}
.banner-info {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  line-height: 0.6rem;
  background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}
.banner-title {
  color: #fff;
  flex: 1;
  padding: 0 0.2rem;
  font-size: 0.36rem;
  margin-bottom: 0.15rem;
}
.img-info {
  margin-top: 0.12rem;
  line-height: 0.4rem;
  height: 0.4rem;
  padding: 0 0.4rem;
  font-size: 0.24rem;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  border-radius: 0.2rem;
}
.img-info span {
  font-size: 0.24rem;
}
</style>
