<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwiper">
      <swiper-slide v-for="item in swiperList" :key="item.id">
        <img :src="item.imgUrl" />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "HomeSwiper",
  props:{
    swiperList:Array
  },
  data() {
    return {
      swiperOption: {
        loop: true,
        pagination: ".swiper-pagination",
        autoplay:3000
      }
    };
  },
  computed: {
    showSwiper(){
      return this.swiperList.length
    }
  },
};
</script>

<style scoped>
.wrapper {
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 31.25%;
  background-color: #ccc;
}
.wrapper >>> .swiper-pagination-bullet-active {
  background: #fff;
}
.wrapper img {
  width: 100%;
}
</style>
