<template>
  <div>
    <routerLink tag="div" to="/" class="header-abs" v-show="showAbs">
      <span class="iconfont abs-icon">&#xe6a5;</span>
    </routerLink>
    <div class="header-fixed" v-show="!showAbs" :style="opacityObj">
      景点详情
      <router-link to="/">
        <div class="iconfont fixed-icon">&#xe6a5;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Detail-Header",
  data() {
    return {
      showAbs: true,
      opacityObj: {
        opacity: 0
      }
    };
  },
  methods: {
    showHeaderFixed() {
      const top = document.documentElement.scrollTop;
      if (top > 50) {
        const opacity = top / 140;
        opacity > 1 ? 1 : opacity;
        this.opacityObj = { opacity };
        this.showAbs = false;
      } else {
        this.showAbs = true;
      }
    }
  },
  activated() {
    window.addEventListener("scroll", this.showHeaderFixed);
  },
//   解除全局事件绑定
  deactivated() {
      window.removeEventListener('scroll',this.showHeaderFixed)
  },
};
</script>

<style scoped>
.header-abs {
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  border-radius: 0.4rem;
  line-height: 0.6rem;
  text-align: center;
  background: rgba(0, 0, 0, 0.8);
  width: 0.6rem;
  height: 0.6rem;
}
.abs-icon {
  color: #fff;
  font-size: 0.4rem;
}
.header-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  line-height: 0.8rem;
  background-color: #00bcd4;
  color: #fff;
  text-align: center;
  z-index: 10;
}
.fixed-icon {
  position: absolute;
  left: 0.12rem;
  top: 0;
  font-size: 0.4rem;
  color: #fff;
}
</style>
