<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) in pages" :key="index">
        <div class="icon" v-for="item in page" :key="item.id">
          <div class="icon-img">
            <img :src="item.iconUrl">
            {{item.title}}
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "HomeIcons",
  props:{
    iconList:Array
  },
  data() {
    return {
      swiperOption:{
        autoplay:false
      }
    }
  },
  computed: {
    pages() {
      const pages = [];
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(item);
      });
      return pages;
    }
  }
};
</script>
<style scoped>
.icons >>> .swiper-container {
  height: 0;
  padding-bottom: 50%;
}
.icon {
  overflow: hidden;
  width: 25%;
  padding-bottom: 25%;
  float: left;
  position: relative;
  height: 0;
}
.icon-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0.3rem;
  box-sizing: border-box;
  padding: 0.1rem;
  text-align: center;
  color: #333;
  /* 如果字很多那么就以...的形式省略后面的字 */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.icon-img img {
  height: 80%;
  display: block;
  margin: 0 auto 0.1rem auto;
}
</style>
