<template>
  <div>
    <Header></Header>
    <home-swiper :swiperList="swiperData"></home-swiper>
    <home-icons :iconList="iconData"></home-icons>
    <home-recommend :recommendList="recommendData"></home-recommend>
    <home-weekend :weekendList="weekendData"></home-weekend>
  </div>
</template>

<script>
import Header from "../components/home/Header";
import HomeSwiper from "../components/home/Swiper";
import HomeIcons from "../components/home/Icons";
import HomeRecommend from "../components/home/Recommend";
import HomeWeekend from "../components/home/Weekend";
import Axios from "axios";
import { mapState } from "vuex";
export default {
  name: "Home",
  components: {
    Header,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  computed: {
    ...mapState(["city"])
  },
  data() {
    return {
      swiperData: [],
      iconData: [],
      recommendData: [],
      weekendData: [],
      lastCity: ""
    };
  },
  methods: {
    getListInfoSuc(res) {
      const data = res.data;
      if (data.ret && data.lists) {
        const lists = data.lists;
        this.swiperData = lists.swiperList;
        this.iconData = lists.iconList;
        this.recommendData = lists.recommendList;
        this.weekendData = lists.weekendList;
      }
    },
    getListInfo() {
      Axios.get("/api/index.json?city=" + this.city)
        .then(this.getListInfoSuc)
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.lastCity = this.city;
    this.getListInfo();
  },
  activated() {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city;
      this.getListInfo();
    }
  }
};
</script>
