<template>
<animation>
  <div>
    <detail-banner :sightNameData='sightName' :bannerImgData='bannerImg' :imgsData='gallaryImgs'></detail-banner>
    <detail-header></detail-header>
    <div class="container">
      <detail-list :lists="listData"></detail-list>
    </div>
  </div>
</animation>
</template>

<script>
import DetailBanner from "../components/detail/Banner";
import DetailHeader from "../components/detail/Header";
import DetailList from "../components/detail/List";
import Axios from 'axios'
import Animation from '../components/common/animate/Animation'
export default {
  name: "Detail",
  components: {
    DetailBanner,
    DetailHeader,
    DetailList,
    Animation
  },
  data() {
    return {
      sightName:'',
      bannerImg:'',
      gallaryImgs:[],
      listData: []
    };
  },
  methods: {
    getDetailListSuc(res){
      const lists=res.data
      if(lists.ret && lists.data){
        const listInfo=lists.data
        this.sightName=listInfo.sightName
        this.bannerImg=listInfo.bannerImg
        this.gallaryImgs=listInfo.gallaryImgs
        this.listData=listInfo.categoryList
      }
    }
  },
  mounted() {
    Axios.get('/api/detail.json',{params:this.$route.params.id}).then(this.getDetailListSuc).catch((err) => {
      console.log(err)
    });
  },
};
</script>

<style scoped>
.container {
  height: 50rem;
}
</style>
