<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder="请输入搜索关键字">
    </div>
    <div class="search-content" ref="content" v-show="keyword">
      <ul>
        <li
          class="search-item border-bottom"
          v-for="item in list"
          :key="item.id"
          @click="ChangeCity(item.name)"
        >{{item.name}}</li>
        <li class="search-item border-bottom" v-if="hasList">没有符合的城市</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
import { mapMutations } from "vuex";
export default {
  name: "CitySearch",
  props: {
    cities: Object
  },
  methods: {
    ...mapMutations(["changeCity"]),
    ChangeCity(city) {
      this.changeCity(city);
      this.keyword = "";
      this.$router.push("/");
    }
  },
  data() {
    return {
      keyword: "",
      timer: null,
      list: []
    };
  },
  computed: {
    hasList() {
      if (this.list.length === 0) {
        return true;
      }
      return false;
    }
  },
  mounted() {
    this.scroll = new Bscroll(this.$refs.content);
  },
  watch: {
    keyword() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        const result = [];
        for (const i in this.cities) {
          this.cities[i].forEach(value => {
            if (
              value.spell.indexOf(this.keyword) != -1 ||
              value.name.indexOf(this.keyword) != -1
            ) {
              result.push(value);
            }
          });
        }
        this.list = result;
      }, 100);
    }
  }
};
</script>

<style scoped>
.search {
  height: 0.72rem;
  background-color: #00bcd4;
  padding: 0 0.1rem;
}
.search-input {
  width: 100%;
  line-height: 0.62rem;
  padding: 0 0.1rem;
  border-radius: 0.06rem;
  color: #666;
  text-align: center;
  box-sizing: border-box;
}
.search-content {
  z-index: 1;
  position: absolute;
  overflow: hidden;
  top: 1.52rem;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #eee;
}
.search-item {
  line-height: 0.65rem;
  padding-left: 0.2rem;
  background-color: #fff;
  color: #666;
}
</style>
