<template>
  <div>
    <city-header></city-header>
    <city-search :cities='citiesData'></city-search>
    <city-list :cities='citiesData' :hotcities='hotCitiesData' :targetArea='targetArea'></city-list>
    <city-alphabet :alphabet='citiesData' @change='changeList'></city-alphabet>
  </div>
</template>

<script>
import Axios from 'axios'
import CityHeader from "../components/city/CityHeader";
import CitySearch from "../components/city/Search";
import CityList from '../components/city/List'
import CityAlphabet from '../components/city/Alphabet'
export default {
  name:'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data() {
    return {
      hotCitiesData:[],
      citiesData:{},
      targetArea:''
    }
  },
  methods: {
    getCityInfo(res){
      const data=res.data
      if(data.ret && data.citylist){
        const citylist=data.citylist
        this.citiesData=citylist.cities
        this.hotCitiesData=citylist.hotCities
      }
    },
    changeList(word){
      this.targetArea=word
    }
  },
  mounted() {
    Axios.get('/api/city.json').then(this.getCityInfo).catch((err) => {
      console.log(err)
    });
  },
};
</script>

