<template>
  <ul class="list">
    <li
      class="item"
      v-for="item in alphabets"
      :key="item"
      :ref="item"
      @click="jumpList"
      @touchstart="TouchStart"
      @touchmove="TouchMove"
      @touchend="TouchEnd"
    >{{item}}</li>
  </ul>
</template>

<script>
export default {
  name: "CityAlphabet",
  props: {
    alphabet: Object
  },
  data() {
    return {
      touchStatus: false,
      startY: 0,
      timer: null
    };
  },
  updated() {
    this.startY = this.$refs["A"][0].offsetTop;
  },
  methods: {
    jumpList(e) {
      this.$emit("change", e.target.innerText);
    },
    TouchStart() {
      this.touchStatus = true;
    },
    TouchMove(e) {
      if (this.touchStatus) {
        if (this.timer) {
          clearTimeout(this.timer);
        }
        this.timer = setTimeout(() => {
          const touchY = e.touches[0].clientY - 76;
          const index = Math.floor((touchY - this.startY) / 20);
          if (index >= 0 && index < this.alphabets.length) {
            this.$emit("change", this.alphabets[index]);
          }
        }, 15);
      }
    },
    TouchEnd() {
      this.touchStatus = false;
    }
  },
  computed: {
    alphabets() {
      const alphabets = [];
      for (const i in this.alphabet) {
        alphabets.push(i);
      }
      return alphabets;
    }
  }
};
</script>

<style scoped>
.list {
  position: absolute;
  top: 1.52rem;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 0.5rem;
}
.item {
  line-height: 0.4rem;
  color: #00bcd4;
  text-align: center;
}
</style>
